.slide-wrapper::-webkit-scrollbar {
  display: none;
}

.slide-wrapper {
  width: 100%;
  height: 100%;

  overflow-y: scroll;
  -webkit-scroll-snap-type: y mandatory;
  scroll-snap-type: y mandatory;
}

.slide-wrapper > div {
  height: 100%;
}

.slide-wrapper > * {
  scroll-snap-align: center;
}