.sign-up-wrapper {
  width: 100%;
  max-width: 390px;
  height: 100%;
  text-align: center;
  
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.sign-up-form {
  width: 100%;
  max-width: 39rem;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#sign-up-id {
  margin-bottom: 34px;
}

#sign-up-password-check {
  margin-bottom: 20px;
}

#sign-up-answer {
  margin-bottom: 40px;
}

.sign-up-error-message {
  height: 2.6rem;
  color: red;
  font-size: 1.4rem;
  line-height: 1.5rem;
  padding-top: 0.8rem;
  margin-bottom: 1.2rem;
}

.sign-up-input-label[for=sign-up-id] {
  margin-top: 30px;
}